<template>
  <div class="order-content">
    <TheCompiler :tabs='tabs' @changeTabs='handleChangeTabs'></TheCompiler>
    <TheOrderList :orderList='filterOrderList'></TheOrderList>
  </div>
</template>

<script>
import TheCompiler from './TheCompiler.vue'
import TheOrderList from './TheOrderList.vue'

export default {
  data () {
    return {
      tabs: [
        {
          tabtxt: '已出单',
          active: false
        },
        {
          tabtxt: '保障中',
          active: false
        },
        {
          tabtxt: '全部',
          active: true
        }
      ],
      orderList: [
        {
          orderSn: 2018082163731,
          status: '已出单',
          title: '保瑞通(厦门)信息科技有限公司'
        },
        {
          orderSn: 2018082163731,
          status: '保障中',
          title: '保瑞通(厦门)信息科技有限公司'
        },
        {
          orderSn: 2018082163731,
          status: '已出单',
          title: '保瑞通(厦门)信息科技有限公司'
        },
        {
          orderSn: 2018082163731,
          status: '已失效',
          title: '保瑞通(厦门)信息科技有限公司'
        },
        {
          orderSn: 2018082163731,
          status: '保障中',
          title: '保瑞通(厦门)信息科技有限公司'
        }
      ]
    }
  },
  components: {
    TheCompiler,
    TheOrderList
  },
  methods: {
    handleChangeTabs (tabParam) {
      this.tabs.find(tab => tab.active).active = false
      tabParam.active = true
    }
  },
  computed: {
    filterOrderList () {
      let tabtxt = this.tabs.find(tab => tab.active).tabtxt
      return this.orderList.filter(order => {
        if (tabtxt == '全部') {
          return true
        }
        ;
        return order.status == tabtxt
      })
    }
  }
}
</script>

<style scoped="scoped" lang="scss">
  .order-content {
    background: #fff;
    box-shadow: 0 4px 12px 0 rgba(219, 219, 219, 0.25);
  }
</style>
